const BOARD_SIZE = 10
const DEFAULT_CELLS_ARRAY = Array(BOARD_SIZE).fill(Array(BOARD_SIZE).fill(0))
const AVAILABLE_MOVES = ['ArrowUp', 'ArrowDown', 'ArrowRight', 'ArrowLeft',]
const SPEED = 200
let step
let curDirection
let phrase = 'Счет:'
let dead = false

const checkPositionOut = position => {
    switch (true) {
        case position >= BOARD_SIZE:
            return 0
        case position < 0:
            return BOARD_SIZE - 1
        default:
            return position
    }
}

const checkDirection = (curDirection, newDirection) => {
    console.log([curDirection, newDirection])
    switch (newDirection) {
        case AVAILABLE_MOVES[0] :
            if (curDirection === AVAILABLE_MOVES[1]) return curDirection
            break;
        case AVAILABLE_MOVES[1]:
            if (curDirection === AVAILABLE_MOVES[0]) return curDirection
            break;
        case AVAILABLE_MOVES[2]:
            if (curDirection === AVAILABLE_MOVES[3]) return curDirection
            break;
        case AVAILABLE_MOVES[3]:
            if (curDirection === AVAILABLE_MOVES[2]) return curDirection
            break;
    }
    return newDirection
}

const App = () => {
    //или массив объектов - [{x:0, y:0}]
    const [snake, setSnake] = React.useState([[1, 1]])
    const [food, setFood] = React.useState([0, 0])
    const [direction, setDirection] = React.useState(AVAILABLE_MOVES[1])
    const [gameover, setGameover] = React.useState(false)

    const handleKeyDown = (event) => {
        console.log(event.key)
        const index = AVAILABLE_MOVES.indexOf(event.key)
        if (index > -1 && !step) {
            step = true;
            curDirection = checkDirection(curDirection, AVAILABLE_MOVES[index])
            setDirection(curDirection)
        }

    }
    React.useEffect(() => {

       // document.addEventListener('keydown', handleKeyDown)
    }, [gameover])

    React.useEffect(() => {
        document.addEventListener('keydown', handleKeyDown)
    }, [direction])

    React.useEffect(() => {
        const interval = gameLoop()
        step = false;
        return () => clearInterval(interval) //чтобы не создавалась куча setTimeout
    }, [snake]) //input-deps, вдруг не обновится

    const generateFood = () => {
        let newFood
        do {
            newFood = [
                Math.floor(Math.random() * BOARD_SIZE),
                Math.floor(Math.random() * BOARD_SIZE)
            ]
        } while (snake.some(elem => elem[0] === newFood[0] && elem[1] === newFood[1]))
        setFood(newFood)
    }

    const gameLoop = () => {
        const timerId = setTimeout(() => {
            //console.log('gameLoop')
            const newSnake = snake
            let move = []

            switch (direction) {
                case AVAILABLE_MOVES[0]:
                    move = [-1, 0]
                    break;
                case AVAILABLE_MOVES[1]:
                    move = [1, 0]
                    break;
                case AVAILABLE_MOVES[2]:
                    move = [0, 1]
                    break;
                case AVAILABLE_MOVES[3]:
                    move = [0, -1]
                    break;
            }



            const head = [
                checkPositionOut(newSnake[newSnake.length - 1][0] + move[0]),
                checkPositionOut(newSnake[newSnake.length - 1][1] + move[1])
            ]

            if(newSnake.some(elem => elem[0] === head[0] && elem[1] === head[1])){
                console.log("Хана!")
                phrase = 'Игра окончена! Ваш счет:'
                dead = true
                setGameover(true)

                console.log(dead)
                return timerId
            }
            newSnake.push(head)

            let sliceIndex = 1
            if (head[0] === food[0] && head[1] === food[1]) {
                sliceIndex = 0
                generateFood()
            }
            setSnake(newSnake.slice(sliceIndex)) // вызывает useEffect

        }, SPEED)

        return timerId
    }


    return (
        <div className='field'>
            <h1>{phrase} {(snake.length-1) * 10}</h1>

            {DEFAULT_CELLS_ARRAY.map((row, indexR) => (
                <div key={indexR} className='row'>
                    {
                        row.map((cell, indexC) => {
                           // let type = (snake[snake.length - 1][0] === indexR && snake[snake.length - 1][1] === indexC) && 'head'

                          //  if (type !== 'head') {
                                let type = snake.some(
                                    elem => elem[0] === indexR && elem[1] === indexC
                                ) && 'snake' //хотя бы одно совпадение исходя из fn
                                if (type !== 'snake') {
                                    //if (true) type = food , else null
                                    type = (food[0] === indexR && food[1] === indexC) && 'food'
                                }
                           // }

                            return (
                                //При маппинге нужно обязательно указывать ключи в атрибут
                                <Cell key={indexC} type={type}/>
                            )
                        })
                    }
                </div>
            ))}
        </div>
    );
}

//лучше в отдельном файле
const Cell = ({type}) => {
    if(type==='snake' && dead) {
        return <div className={`cell dead`}/>
    }
    return <div className={`cell ${type}`}/>
}

export default App;
